import React, { useState } from 'react';
import styled from 'styled-components';
import MobileSidebar from '../../components/MobileSidebar';
import SamuraiGalleryScreen from './SamuraiGalleryScreen'
import SamuraiDataScreen from './SamuraiDataScreen'
import ShogunateScreen from './ShogunateScreen'
import DesignScreen from './DesignScreen'

const data = {"tokenId":79,"name":"Katana N' Samurai #0079","image":"https://gateway.pinata.cloud/ipfs/QmRFcV8QuCrNwbpjEsU6Q5EZKQGFoqZep2X8LnhWSQT8q2","description":"I used to kill an alien wearing a knitted cap.","attributes":[{"trait_type":"Rankings","value":8178,"max_value":10000},{"trait_type":"Ryoku","value":21,"max_value":66},{"trait_type":"Location","value":"夜の東京湾 Tokyo Wan at Night"},{"trait_type":"Clothes","value":"くまいささぐるま Kuma i-sa saguru ma"},{"trait_type":"Type","value":"人々 Human"},{"trait_type":"Eyes","value":"仁 Compassion"},{"trait_type":"Hats and Hairstyles","value":"カラフルのビーニー Beanie"},{"trait_type":"Accesories","value":"チェーンのめがね＋ペロ Glasses with Chain and Tongue Out"},{"trait_type":"Bushi Ranku","value":"Kashira"},{"trait_type":"Shogunate","value":"oda"}]}

const MySamuraiPage = () => {
  const [ openDataScreen, setOpenDataScreen ] = useState(false);
  const [ currentSection, setCurrentSection ] = useState('samurai');

  function handleOpenDataScreen() {
    setOpenDataScreen(true);
  }
  function handleCloseDataScreen() {
    setOpenDataScreen(false);
  }

  function handleEnterSamurai() {
    setCurrentSection('samurai');
  }

  function handleEnterShogunate() {
    setCurrentSection('shogunate');
  }
  
  function handleEnterDesign() {
    setCurrentSection('design');
  }
  
  return (
    <Root>
      { currentSection === 'samurai' &&
        <SamuraiScreen>
          <MobileSidebar
            title="MY SAMURAI / #3267"
            onPrev={handleCloseDataScreen}
            open={openDataScreen}>
            <SamuraiDataScreen data={data} onNext={handleEnterShogunate} />
          </MobileSidebar>
          <SamuraiGalleryScreen data={data} onSelected={handleOpenDataScreen} />
        </SamuraiScreen>
      }
      { currentSection === 'shogunate' && 
        <MobileSidebar
          title="#3267 / SHOGUNATE"
          onPrev={handleEnterSamurai}>
          <ShogunateScreen data={data} 
            onPrev={handleEnterSamurai}
            onNext={handleEnterDesign} />
        </MobileSidebar>
      }
      { currentSection === 'design' && 
        <MobileSidebar
          title="#3267 / DESIGN"
          onPrev={handleEnterShogunate}>
          <DesignScreen data={data} onPrev={handleEnterShogunate} />
        </MobileSidebar>

      }
    </Root>
  )
}

const Root = styled.div`

`

const SamuraiScreen = styled.div`
`

export default MySamuraiPage;